.card {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}

.card__img {
  border-radius: 20px;
  box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.05);
  object-fit: cover;
  aspect-ratio: 37 /18;
}

.card__body {
  display: flex;
  flex-direction: column;
  padding: 20px 24px;
  flex-grow: 1;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.05);
}

.card__title {
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 800;
  line-height: 1.2;
  color: #000000;
}

.card__price {
  display: block;
  margin-bottom: 25px;
  font-size: 20px;
  line-height: 1.2;
  color: #00ae6c;
}

.card__list {
  margin-bottom: 25px;
  padding-left: 22px;
  display: flex;
  flex-direction: column;
  row-gap: 14px;
  font-size: 14px;
  line-height: 1.2;
}

.card__list-item {
  list-style: disc;
}

.card__button {
  margin-top: auto;
}
