.nav-footer {
  display: flex;
  align-items: center;
  column-gap: 40px;
  font-size: 14px;

  @include mobile {
    flex-direction: column;
    row-gap: 15px;
  }
}

.nav-footer__item--only-tablet-mobile {
  display: none;

  @include tablet {
    display: block;
  }
}

.nav-footer__link {
  transition: color 0.2s ease-in;

  &:hover {
    color: #00ae6c;
  }
}
