.services {
  @include scrollTopMargin;
}

.services__title {
  margin-bottom: 40px;
  max-width: 570px;

  @include mobile {
    margin-bottom: 20px;
  }
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px 30px;

  @include mobile {
    gap: 20px;
  }
}

.services__list-item {
  max-width: 270px;

  @include tablet {
    max-width: 354px;
  }
}
