.service__icon {
  margin-bottom: 20px;

  @include mobile {
    margin-bottom: 10px;
  }
}

.service__title {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 800;
  line-height: 1;
  color: #000000;

  @include tablet {
    font-size: 18px;
  }
}

.service__desc {
  color: #2f2f2f;
  font-size: 14px;
  line-height: 1.5;

  p + p {
    margin-top: 0.5em;
  }
}
