.map {
  @include scrollTopMargin;
}

.map__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 20px;

  @include mobile {
    row-gap: 10px;
  }
}

.map__desc {
  font-size: 16px;
  line-height: 1.5;

  p + p {
    margin-top: 0.5em;
  }

  @include mobile {
    font-size: 14px;
  }
}

.map__iframe {
  width: 100%;
  height: 400px;
  border: none;
  border-radius: 40px;

  @include mobile {
    border-radius: 20px;
  }
}
