@mixin tablet() {
  @media (max-width: 1220px) {
    @content;
  }
}

@mixin mobile() {
  @media (max-width: 788px) {
    @content;
  }
}

@mixin mediaBG() {
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolusion: 192dpi) {
    @content;
  }
}

@mixin scrollTopMargin() {
  scroll-margin-top: 60px;

  @include tablet {
    scroll-margin-top: 40px;
  }

  @include mobile {
    scroll-margin-top: 30px;
  }
}
