.topic {
  padding: 30px 0;
  border-top: 1px solid #ececf1;
  border-bottom: 1px solid #ececf1;

  @include tablet {
    padding-top: 17px;
    padding-bottom: 17px;
  }

  @include mobile {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

.topic + .topic {
  margin-top: -1px;
}

.topic__btn {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 30px;

  &:hover:not(:focus-visible) {
    opacity: 0.5;
  }

  &:active:not(:focus-visible) .topic__title {
    color: #00ae6c;
  }

  &:focus {
    outline: none;
  }

  &:focus-visible {
    outline-offset: 1px;
    outline: #00ae6c auto 1px;
  }
}

.topic__title {
  font-size: 22px;
  font-weight: 700;
  line-height: 1;
  color: #000000;

  @include mobile {
    font-size: 18px;
  }
}

.topic__icon {
  @include mobile {
    width: 32px;
    height: 32px;
  }
}

.topic__content {
  display: flex;
  flex-direction: column;
  gap: 1em;
  line-height: 1.5;
  max-height: 0;
  overflow: hidden;
  transition: all 0.4s ease-in;

  @include mobile {
    padding-top: 13px;
    font-size: 14px;
  }
}

.topic__content-wrapper {
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.topic .topic__content-link {
  color: #00ae6c;
  text-decoration: underline;
}
