.price {
  padding: 60px 0 120px;
  background-color: #f6f6f6;
  @include scrollTopMargin;

  @include tablet {
    padding-bottom: 80px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 60px;
  }
}

.price__title {
  margin-bottom: 40px;

  @include mobile {
    margin-bottom: 20px;
  }
}

.price__cards {
  display: flex;
  flex-wrap: wrap;
  column-gap: 30px;

  & > article {
    flex: 1;

    @include tablet {
      min-width: 354px;
    }

    @include mobile {
      min-width: unset;
    }
  }

  @include tablet {
    row-gap: 50px;
  }

  @include mobile {
    row-gap: 30px;
  }
}
