.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: all 0.2s ease-in;

  &--open {
    opacity: 1;
    pointer-events: all;
  }
}

.modal__body {
  position: relative;
  padding: 40px;
  width: 420px;
  color: #2f2f2f;
  background-color: #ffffff;
  border-radius: 20px;
  transition: all 0.2s ease-in;
  transform: translate(0, -50%);

  @include mobile {
    padding: 20px;
    width: 360px;
  }
}

.modal--open .modal__body {
  transform: translate(0, 0%);
}

.modal__button-close {
  position: absolute;
  top: 14px;
  right: 14px;

  @include mobile {
    top: 10px;
    right: 10px;
  }
}

.modal__header {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  text-align: center;
  font-size: 14px;

  p + p {
    margin-top: 0.5em;
  }
}

.modal__title {
  font-size: 36px;
  font-weight: 800;
  line-height: 1.2;
  color: #000000;

  @include mobile {
    font-size: 28px;
  }
}
